import NcSelect from '@nextcloud/vue/components/NcSelect';
import moment from 'moment-timezone';
const zones = moment.tz.names();
export default {
  name: 'RequestTime',
  components: {
    NcSelect
  },
  props: {
    modelValue: {
      type: String,
      default: '[]'
    }
  },
  emits: ['update:model-value'],
  data() {
    return {
      timezones: zones,
      valid: false,
      newValue: {
        startTime: null,
        endTime: null,
        timezone: moment.tz.guess()
      },
      stringifiedValue: '[]'
    };
  },
  watch: {
    modelValue() {
      this.updateInternalValue();
    }
  },
  beforeMount() {
    // this is necessary to keep so the value is re-applied when a different
    // check is being removed.
    this.updateInternalValue();
  },
  methods: {
    updateInternalValue() {
      try {
        const data = JSON.parse(this.modelValue);
        if (data.length === 2) {
          this.newValue = {
            startTime: data[0].split(' ', 2)[0],
            endTime: data[1].split(' ', 2)[0],
            timezone: data[0].split(' ', 2)[1]
          };
          this.stringifiedValue = `["${this.newValue.startTime} ${this.newValue.timezone}","${this.newValue.endTime} ${this.newValue.timezone}"]`;
          this.validate();
        }
      } catch (e) {
        // ignore invalid values
      }
    },
    validate() {
      this.valid = this.newValue.startTime && this.newValue.startTime.match(/^(0[0-9]|1[0-9]|2[0-3]|[0-9]):[0-5][0-9]$/i) !== null && this.newValue.endTime && this.newValue.endTime.match(/^(0[0-9]|1[0-9]|2[0-3]|[0-9]):[0-5][0-9]$/i) !== null && moment.tz.zone(this.newValue.timezone) !== null;
      if (this.valid) {
        this.$emit('valid');
      } else {
        this.$emit('invalid');
      }
      return this.valid;
    },
    update() {
      if (this.newValue.timezone === null) {
        this.newValue.timezone = moment.tz.guess();
      }
      if (this.validate()) {
        this.stringifiedValue = `["${this.newValue.startTime} ${this.newValue.timezone}","${this.newValue.endTime} ${this.newValue.timezone}"]`;
        this.$emit('update:model-value', this.stringifiedValue);
      }
    }
  }
};