import NcEllipsisedOption from '@nextcloud/vue/components/NcEllipsisedOption';
import NcSelect from '@nextcloud/vue/components/NcSelect';
import valueMixin from "../../mixins/valueMixin.js";
export default {
  name: 'RequestUserAgent',
  components: {
    NcEllipsisedOption,
    NcSelect
  },
  mixins: [valueMixin],
  props: {
    modelValue: {
      type: String,
      default: ''
    }
  },
  emits: ['update:model-value'],
  data() {
    return {
      newValue: '',
      predefinedTypes: [{
        id: 'android',
        label: t('workflowengine', 'Android client'),
        icon: 'icon-phone'
      }, {
        id: 'ios',
        label: t('workflowengine', 'iOS client'),
        icon: 'icon-phone'
      }, {
        id: 'desktop',
        label: t('workflowengine', 'Desktop client'),
        icon: 'icon-desktop'
      }, {
        id: 'mail',
        label: t('workflowengine', 'Thunderbird & Outlook addons'),
        icon: 'icon-mail'
      }]
    };
  },
  computed: {
    options() {
      return [...this.predefinedTypes, this.customValue];
    },
    matchingPredefined() {
      return this.predefinedTypes.find(type => this.newValue === type.id);
    },
    isPredefined() {
      return !!this.matchingPredefined;
    },
    customValue() {
      return {
        icon: 'icon-settings-dark',
        label: t('workflowengine', 'Custom user agent'),
        id: ''
      };
    },
    currentValue: {
      get() {
        if (this.matchingPredefined) {
          return this.matchingPredefined;
        }
        return {
          icon: 'icon-settings-dark',
          label: t('workflowengine', 'Custom user agent'),
          id: this.newValue
        };
      },
      set(value) {
        this.newValue = value;
      }
    }
  },
  methods: {
    validateRegex(string) {
      const regexRegex = /^\/(.*)\/([gui]{0,3})$/;
      const result = regexRegex.exec(string);
      return result !== null;
    },
    setValue(value) {
      // TODO: check if value requires a regex and set the check operator according to that
      if (value !== null) {
        this.newValue = value.id;
        this.$emit('update:model-value', this.newValue);
      }
    },
    updateCustom() {
      this.newValue = this.currentValue.id;
      this.$emit('update:model-value', this.newValue);
    }
  }
};