import NcEllipsisedOption from '@nextcloud/vue/components/NcEllipsisedOption';
import NcSelect from '@nextcloud/vue/components/NcSelect';
import valueMixin from "../../mixins/valueMixin.js";
export default {
  name: 'RequestURL',
  components: {
    NcEllipsisedOption,
    NcSelect
  },
  mixins: [valueMixin],
  props: {
    modelValue: {
      type: String,
      default: ''
    },
    operator: {
      type: String,
      default: ''
    }
  },
  emits: ['update:model-value'],
  data() {
    return {
      newValue: '',
      predefinedTypes: [{
        icon: 'icon-files-dark',
        id: 'webdav',
        label: t('workflowengine', 'Files WebDAV')
      }]
    };
  },
  computed: {
    options() {
      return [...this.predefinedTypes, this.customValue];
    },
    placeholder() {
      if (this.operator === 'matches' || this.operator === '!matches') {
        return '/^https\\:\\/\\/localhost\\/index\\.php$/i';
      }
      return 'https://localhost/index.php';
    },
    matchingPredefined() {
      return this.predefinedTypes.find(type => this.newValue === type.id);
    },
    isPredefined() {
      return !!this.matchingPredefined;
    },
    customValue() {
      return {
        icon: 'icon-settings-dark',
        label: t('workflowengine', 'Custom URL'),
        id: ''
      };
    },
    currentValue() {
      if (this.matchingPredefined) {
        return this.matchingPredefined;
      }
      return {
        icon: 'icon-settings-dark',
        label: t('workflowengine', 'Custom URL'),
        id: this.newValue
      };
    }
  },
  methods: {
    validateRegex(string) {
      const regexRegex = /^\/(.*)\/([gui]{0,3})$/;
      const result = regexRegex.exec(string);
      return result !== null;
    },
    setValue(value) {
      // TODO: check if value requires a regex and set the check operator according to that
      if (value !== null) {
        this.newValue = value.id;
        this.$emit('update:model-value', this.newValue);
      }
    },
    updateCustom(event) {
      this.newValue = event.target.value;
      this.$emit('update:model-value', this.newValue);
    }
  }
};